function enableTileOverflow() {
    console.log('enableTileOverflow()')
    document.getElementsByClassName('tiles-container')[0].style.clipath = '5%'
}

function disableTileOverflow() {
    console.log('disableTileOverflow()')
    document.getElementsByClassName('tiles-container')[0].style.left = 0
}



const MyModal = () => {
  
    const [open, setOpen] = useState(false)

    function showModal() {
        setOpen(true)
    }

    function handleOk(e) {
        console.log(e);
        setOpen(false)
    }

    function handleCancel(e) {
        console.log(e);
        setOpen(false)
    }

    return (
        <div>
            <Button type="primary" onClick={showModal}>
                Open Modal
            </Button>
            <Modal
                title="Basic Modal"
                visible={open}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <p>Some contents...</p>
                <p>Some contents...</p>
                <p>Some contents...</p>
            </Modal>
        </div>
    )
}

const Tile = ({ tile }) => {
    const [open, setOpen] = useState(false)

    return (
        <>
            {/* <div onMouseEnter={enableTileOverflow} onMouseOut={disableTileOverflow} className='tile' */}
            <div className='tile' onClick={() => setOpen(!open)}>
                <img className='tile-image' src={tile.source} />
                <div className='tile-details'>
                    <div className='tile-title'>{tile.title}</div>
                </div>
            </div>
            {/* <MyModal /> */}
            <Modal
                title="Vertically centered modal dialog"
                centered
                visible={open}
                onOk={() => setOpen(true)}
                onCancel={() => setOpen(false)}
                zIndex={99999}
                mask={true}
                closable={false}
                // maskClosable={true}
            >
                <p>some contents...</p>
                <p>some contents...</p>
                <p>some contents...</p>
            </Modal>
        </>
    )
}

